[class^="page-section"] {
    width: 100%;
    display: block;
    position: relative;
    text-align: center;

    > div {
        overflow: hidden;
    }
}

.page-section__hero {
    min-height: $hero__height;
    max-width: 100%;
}

.page-section__main-content {
    min-height: 100vh;
    height: 100%;
}
